import React from "react"
import PropTypes from "prop-types"
import { Link, Button } from "gatsby-theme-material-ui"
import AppBar from "@material-ui/core/AppBar"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import { makeStyles } from "@material-ui/core/styles"
import IconButton from "@material-ui/core/IconButton"
import MoreIcon from "@material-ui/icons/MoreVert"
import MenuItem from "@material-ui/core/MenuItem"
import Menu from "@material-ui/core/Menu"

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
}))

const NavBar = ({ title }) => {
  const classes = useStyles()

  const [anchorEl, setAnchorEl] = React.useState(null)

  const handleClick = event => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const mobileMenuId = "mobile-menu"
  const renderMobileMenu = (
    <Menu
      id={mobileMenuId}
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={handleClose} component={Link} to="/about">
        About Me
      </MenuItem>
      <MenuItem onClick={handleClose} component={Link} to="/book-reviews">
        Book Reviews
      </MenuItem>
      <MenuItem onClick={handleClose} component={Link} to="/blog">
        Blog
      </MenuItem>
    </Menu>
  )

  return (
    <AppBar id="AppBar" position="sticky" color="inherit">
      <Toolbar>
        <Typography variant="h6" component="h1">
          <Button color="inherit" to="/">
            {title}
          </Button>
        </Typography>
        <div className={classes.grow} />
        <div className={classes.sectionDesktop}>
          <Button color="inherit" to="/about">
            About Me
          </Button>
          <Button color="inherit" to="/book-reviews">
            Book Reviews
          </Button>
          <Button color="inherit" to="/blog">
            Blog
          </Button>
        </div>
        <div className={classes.sectionMobile}>
          <IconButton
            aria-label="show more"
            aria-controls={mobileMenuId}
            aria-haspopup="true"
            onClick={handleClick}
            color="inherit"
          >
            <MoreIcon />
          </IconButton>
        </div>
      </Toolbar>
      {renderMobileMenu}
    </AppBar>
  )
}

NavBar.propTypes = {
  title: PropTypes.string.isRequired,
}

export default NavBar
